.filters label {
  font-weight: 500;
  margin-left: 8px;
  padding: 7px 10px;
  border: 1px solid transparent;
  border-radius: 3px;
  color: #0052CC;
}
.filters label:hover {
  border: 1px solid #DFE1E5;
  color: #0065FF;
  text-decoration: none;
}

.filters .checked {
  color: #FFF;
  background: #253858;
}
.filters .checked:hover {
  border: 1px solid transparent;
  color: #091E42;
  background: #DFE1E5;
}

.filters input {
  display: none;
}
